/* Imports ----------------------------------------------------------------- */

// D3 imports commented out for development: UNCOMMENT for production
// import "whatwg-fetch";
import * as d3 from "d3";

import { AnimatedColumnChart } from "animated-bars";
import { chartColors } from "./clcharts-d3-colors.js";
import { data } from "./data.js";
import "./style.css";

/* Constants --------------------------------------------------------------- */

const CHART_ELEMENT = "clchart";
const CONTAINER_MAX_WIDTH = 900;
const CONTAINER_SMALL_TEXT = 600;
const FILE_DATA = "global-temperature.csv";
const COLOR_INACTIVE_POS = "#eb3423";
const COLOR_INACTIVE_NEG = "#4cb0ea";
const COLOR_ACTIVE_POS = "#eb3423";
const COLOR_ACTIVE_NEG = "#4cb0ea";

/* Function for generating normal random variables ------------------------- */

const normal = (() => {

    /*
    Return a normal random variable with the given mean and standard deviation.
    The function generates a pair of normal random variables using the polar
    method and stores the spare variable in a closure for the next call.
    */

    let spare;
    let hasSpare = false;

    return (mean, std) => {

        let x, y, s, m;

        if (hasSpare) {

            hasSpare = false;
            return mean + std * spare;

        } else {

            do {
                x = Math.random() * 2 - 1;
                y = Math.random() * 2 - 1;
                s = x * x + y * y;
            } while (s >= 1 || s == 0);

            m = Math.sqrt(-2.0 * Math.log(s) / s);
            spare = y * m;
            hasSpare = true;
            return mean + std * x * m;
        }
    };
})();

function asymmetricNormal(mean, lower, upper) {

    if (Math.random() < 0.5) {
        return mean + (Math.abs(normal(0, lower)) * -1);
    } else {
        return mean + Math.abs(normal(0, upper));
    }
}

window.asymmetricNormal = asymmetricNormal;

/* Generator --------------------------------------------------------------- */

function* randomDataGenerator(data) {
    while (true) {
        const nextData = [];
        data.forEach(d => {
            nextData.push({
                key: d.key,
                value: asymmetricNormal(+d.value, +d.lower, +d.upper),
                posRgb: COLOR_ACTIVE_POS,
                negRgb: COLOR_ACTIVE_NEG
            });
        });
        yield nextData;
    }
}

/* Draw graphic ------------------------------------------------------------ */

function drawGraphic(data, containerWidth, scale) {

    const config = {
        element: `${CHART_ELEMENT}-vis`,
        dimensions: {
            width: containerWidth,
            height: 600 * scale
        },
        margins: {
            top: 105 * scale,
            right: 20 * scale,
            bottom: 100 * scale,
            left: 56
        },
        colors: {
            posRgb: COLOR_INACTIVE_POS,
            negRgb: COLOR_INACTIVE_NEG
        },
        background: chartColors.background,
        title: "Uncertainty affects annual estimates, but not the warming trend",
        titleSize: 22 * scale,
        titleColor: chartColors.title,
        titleOffsetX: 18,
        titleOffsetY: 40 * scale,
        subtitle: "Global temperature anomaly v 1961-1990 average (C), HadCRUT4 data series",
        subtitleSize: 18 * scale,
        subtitleColor: chartColors.title,
        subtitleOffsetX: 18,
        subtitleOffsetY: 30 * scale,
        keyTitle: null,
        keyTextSize: (containerWidth > CONTAINER_SMALL_TEXT) ? 11 : 8,
        keyTextColor: chartColors.axisText,
        keyLineColor: chartColors.axisLine,
        keyTickSizeInner: 0,
        keyTickSizeOuter: 0,
        keyTickPadding: 18 * scale,
        valueTitle: null,
        valueMin: -0.8,
        valueMax: 1.2,
        valueTextSize: (containerWidth > CONTAINER_SMALL_TEXT) ? 11 : 8,
        valueTextColor: chartColors.axisText,
        valueLineColor: chartColors.axisLine,
        valueTicks: 10,
        valueFormat: ",",
        valueTickSizeOuter: 0,
        valueFormat:".1f",
        valuePaddingInner: 0.25,
        valuePaddingOuter: 0.5,
        valueTickPadding: 10,
        shapeRendering: "geometricPrecision"
    };


    // Clear the visualisation
    d3.select(`#${CHART_ELEMENT}-vis`).selectAll("*").remove();

    // Create the visualisation
    const chart = new AnimatedColumnChart(data, config);
    chart.create();
    chart.keepValueAxisTicks([0,2,4,6,8,10]);
    chart.keepKeyAxisTicks([0, 20,40,60,80,100,120,140,160]);

    // Add run label
    const runLabel = chart.selections.dataGroup.append("text")
        .attr("transform", `translate(
            ${chart.width * 0.78},
            ${chart.height * 0.95 })`)
        .attr("class", "run-label")
        .style("text-anchor", "middle")
        .style("font-weight", "bold")
        .style("font-size", `${14 * scale / 0.8}pt`)
        .style("fill", "#999999")
        .attr("opacity", 1.0)
        .text("Click or tap the chart to run");

    // Add explanation
    const explanation = chart.selections.svg.append("text")
        .attr("transform", `translate(
            ${chart.titleOffsetX},
            ${chart.dimensions.height * 0.965 })`)
        .attr("class", "explanation")
        .style("text-anchor", "left")
        .style("font-size", `${9 * scale / 0.8}pt`)
        .style("fill", "#666666")
        .attr("opacity", 1.0)
        .text("When the chart animates, it shows different ways the data might have looked within the estimated range of uncertainty.");

    // Add event handler to start on click
    chart.selections.svg.on("click", () => {
        if (chart.running) {
            chart.stop();
            chart.update(data);
            showRunLabel(runLabel)
        } else {
            hideRunLabel(runLabel)
            chart.run(randomDataGenerator(data));
        }
    });
}

/* Show / hide run label --------------------------------------------------- */

function showRunLabel(runLabel) {
    runLabel
        .interrupt()
        .transition()
        .delay(500)
        .duration(500)
        .attr("opacity", 1.0);
}

function hideRunLabel(runLabel) {
    runLabel
        .interrupt()
        .transition()
        .duration(500)
        .attr("opacity", 0);
}

/* Draw graphic in container context --------------------------------------- */

function getDrawGraphicInContext() {

    let lastContainerWidth = 0;

    const drawGraphicInContext = function(data) {

        // Get the actual width of the visualisation container
        const containerWidth = document
            .getElementById(`${CHART_ELEMENT}-vis`)
            .getBoundingClientRect()
            .width;
        
        // Only redraw the graphic if the container width has changed
        if (containerWidth == lastContainerWidth) return;
        lastContainerWidth = containerWidth;

        // Calculate scale as a percentage of maximum width
        const scale = containerWidth / CONTAINER_MAX_WIDTH;

        drawGraphic(data, containerWidth, scale);
    };

    return drawGraphicInContext;
}

/* Main -------------------------------------------------------------------- */

function main() {

    const element = d3.select(`#${CHART_ELEMENT}`);
    const drawGraphicInContext = getDrawGraphicInContext()

    // Create the visualisation containers
    element.append("div")
        .attr("id", `${CHART_ELEMENT}-container`)
        .style("max-width", `${CONTAINER_MAX_WIDTH}px`)
        .append("div")
        .attr("id", `${CHART_ELEMENT}-vis`)
        .style("max-width", "100%");

    // Listen for a resize event and redraw when fired
    window.addEventListener("resize", () => {
        drawGraphicInContext(data);
    });

    // Do the initial draw
    drawGraphicInContext(data);
}


main(data);
